@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
body {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  background-color: aliceblue;
}
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Sora", sans-serif;
}

.champion__card {
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: lightgray;
  width: 40vw;
}

.champion__card img {
  max-width: 75px;
  max-height: 75px;
  width: 100%;
  height: 100%;

  border-radius: 99%;
}

.summoner__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid gray;
  padding-left: 30px;
  padding-right: 30px;
  /* background-image: url("https://raw.communitydragon.org/latest/plugins/rcp-be-lol-game-data/global/default/v1/summoner-backdrops/552.jpg"); */
  background-position: center center;
  background-size: cover;
  color: white;
  height: 250px;
  border-bottom: none;
}

.summoner__title h3 {
  padding-right: 15px;
  padding-left: 15px;
  font-size: 1.2rem;
}

.map__info {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.match__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 30px 30px 30px;
  background: #5f0808;
}

.summoner__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.summoner__form input {
  padding: 15px 25px;
  font-weight: 900;
  font-family: "Poppins", sans-serif;
  width: 100%;
}

::-webkit-input-placeholder {
  background-image: linear-gradient(90deg, red, blue);
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
}

:-ms-input-placeholder {
  background-image: linear-gradient(90deg, red, blue);
  background-clip: text;
  color: transparent;
}

::placeholder {
  background-image: linear-gradient(90deg, red, blue);
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
}

.match__container {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: darkgray;
}

.match__container > div {
  display: flex;
}

.match__container > div p {
  font-weight: 900;
  margin-bottom: 10px;
  font-family: monospace;
}

.test1 {
  display: flex;
  border-top: 1px solid darkgray;
  width: 100%;
  justify-content: space-between;
}

.test2 {
  display: flex;
  border-top: 1px solid darkgray;
  width: 100%;
  justify-content: space-between;
}

/* MATCH INFORMATION */
.name {
  color: rgb(0, 0, 0);
  text-align: left;
  text-align: initial;
  max-width: 120px;
  width: 100%;
  padding-left: 10px;
}

.name p {
  border: none !important;
}

.kills {
  color: green;
}

.deaths {
  color: red;
}

.assists {
  color: yellow;
}
.gold__earned p {
  color: yellow;
}

.scoreboard__champ-id p {
  list-style: none;
  padding-right: 3px;
}

.total__games {
  display: flex;
  flex-direction: column;
}

.rank__container {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #5f0808;
}

.rank__info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.rank__info h5 {
  padding-top: 10px;
  margin: 0;
}

.scoreboard__champ__id {
  display: flex;
  flex-direction: column !important;
}

.scoreboard__champ__img {
  max-width: 30px;
  max-height: 30px;
}

.scoreboard__champ__img img {
  width: 100%;
  height: 100%;
}

.lane__info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.lane__info h4 {
  margin-bottom: 5px;
}

.champ__info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.winning__container {
  background: #5f0808;
}

.champion__mastery__container {
  background: darkcyan;
}

